import React from "react"
import { Link } from "gatsby"

import { rhythm, scale } from "../utils/typography"
import SignupForm from "./signup-form"

import "./layout.css"

class Layout extends React.Component {

  state = {
    menuOpen: false
  }

  setMenuOpen = (val) => {
    this.setState({menuOpen: val});
  }

  toggleMenu() {
    let val = this.state.menuOpen;
    this.setMenuOpen(!val);
  }

  render() {
    const { location, title, children } = this.props
    const rootPath = `${__PATH_PREFIX__}/`
    let header = (
      <>
        <h1
          style={{
            ...scale(0.75),
            marginBottom: rhythm(1),
            marginTop: 0,
            display: "inline-block"
          }}
        >
          <Link
            style={{
              boxShadow: `none`,
              textDecoration: `none`,
              color: `inherit`,
            }}
            to={`/`}
          >
            {title}
          </Link>
        </h1>
        <div id="large-nav">
          <Link className="large-nav-link" to={`/posts/`}>Posts</Link>
          {' '}&middot;{' '}
          <Link className="large-nav-link" to={`/talks/`}>Talks</Link>
          {' '}&middot;{' '}
          <Link className="large-nav-link" to={`/publications/`}>Publications</Link>
        </div>
        <div id="small-nav">
          <div id="small-nav-root" onClick={ () => this.toggleMenu()}>&nbsp;</div>
          <div id="small-nav-menu" style={{
            display: this.state.menuOpen ? "block" : "none"
          }}>
            <Link className="small-nav-link" to={`/posts/`}>Posts</Link>
            {' '}&middot;{' '}
            <Link className="small-nav-link" to={`/talks/`}>Talks</Link>
            {' '}&middot;{' '}
            <Link className="small-nav-link" to={`/publications/`}>Publications</Link>
          </div>
        </div>
      </>
    )
    return (
      <div
        style={{
          marginLeft: "auto",
          marginRight: "auto",
          maxWidth: rhythm(40),
          padding: `${rhythm(1.5)} ${rhythm(3 / 4)}`,
        }}
      >
        <header>{header}</header>
        <hr></hr>
        <main>{children}</main>
        <hr></hr>
        <footer style={{
          maxWidth: "800px",
          marginLeft: "auto",
          marginRight: "auto"
        }}>
          © Anubhav Singh {new Date().getFullYear()}
          <div style={{
            display: "inline-block",
            float: "right"
          }}>
            <a
              style={{
                boxShadow: `none`,
                textDecoration: `none`,
                color: `inherit`,
              }}
              href="/sitemap.xml"
            >
              Sitemap
            </a>
          </div>
        </footer>
      </div>
    )
  }
}

export default Layout
